                      callList[key][key2][key3][element].filter(
                        (item) => item.CSLB_Channel_Type__c == elemente
                      ).length
                    );
                  }

                  const sum = fiterSum.reduce((sum, num) => sum + num, 0);
                  obj[element] = sum;
                } else {
                  obj[element] = callList[key][key2][key3][element].length;
                }

                dataObj.push(
                  callList[key][key2][key3][element]
                    .map((p) => p["CSLB_Channel_Type__c"])
                    .filter((v) => v)
                );
              }

              const dataCategory = [];

              for (const element of dataObj) {
                for (const element2 of element) {
                  if (!dataCategory.includes(element2)) {
                    dataCategory.push(element2);
                  }
                }
              }

              const rec = {
                営業部: props.id,
                エリア: key,
                テリトリー名: key2,
                MR: key3,
                data: obj,
                チャネル: dataCategory,
              };
              result.push(rec);
            }
          }
        }

        return result
